.mypageSec{
  padding: 0px 10px;
}
/* .registerSec:nth-child(2){
  margin-bottom: 150px;
} */



.mypageSec h2{
  font-size: 28px;
  font-weight: bold;
  color: #0042EC;
  margin-top: 20px;
  margin-bottom: 50px;
}

.category {
  color: #191D23;
  font-weight: bold;
}

.box {
  width: 100%;
  height: 48px;
  padding: 0 10px;
  margin-top: 5px;
  box-sizing: border-box;
  margin-bottom: 16px;
  border-radius: 6px;
  border: 1px solid #D0D5DD;
  background-color: #F8F8F8;
  align-items: center;
  display: flex;
}

#gender ul {
  display: flex;
  margin-bottom: 16px;
}

#gender ul li {
  width: 48%;
  height: 45px;
  line-height: 45px;
  text-align: center;
  cursor: pointer;
  margin-top: 5px;
  border-radius: 6px;
}

.gender ul li:nth-child(1){
  margin-left: 0;
}

.Female {
  background-color: #0042EC;
  color: #fff;
}

.Male {
  background-color: #eee;
}


.gender ul li:nth-child(2){
  margin-right: 0;
}


#job ul {
  display: flex;
  margin-bottom: 16px;
  align-items: center;
  margin-right: 8px;
}

#job ul li {
  width: 23%;
  height: 45px;
  line-height: 45px;
  text-align: center;
  cursor: pointer;
  margin-top: 5px;
  border-radius: 6px;
}

.Teen, .Worker, .Etc {
  background-color: #eee;
}

.Student {
  background-color: #0042EC;
  color: #fff;
}

.job ul li:nth-last-child(1){
  margin-right: 0px;
}

#job ul li:nth-child(4){
  margin-right: 0px;
}



#age ul {
  display: flex;
  margin-bottom: 16px;
}

#age ul li {
  width: 48%;
    height: 45px;
    line-height: 45px;
    text-align: center;
    cursor: pointer;
    margin-top: 5px;
    border-radius: 6px;
}

.a10s, .a30s, .a40s, .a50s, .a60s {
  background-color: #eee;
}

.a20s {
  background-color: #0042EC;
  color: #fff;
}



#age ul{
  display: grid;
  width: 100%;
  grid-template-columns: repeat(3, 1fr); /* 3개의 열을 가진 그리드 */
  gap: 4%; /* 열 사이의 간격 조절 */
}

#age ul li{
  width: 100%;
}

#age ul li:nth-child(4){
  margin-left: 0;
}

#age ul li:nth-child(3), li:nth-child(6){
  margin-right: 0;
}

button {
  width: 100%;
  height: 55px;
  color: #fff;
  font-size: 16px;
  border-radius: 6px;
  background-color: #0042EC;
  margin-top: 20px;
  cursor: pointer;
  border: none;
}
